import { buildModel } from "@/model/buildModel.js"

export default {
  data () {
    return {
      swiperIndex: 0
    }
  },
  props: {
    list: {
      type: Array,
      default: []
    },
    desc: {
      type: Object,
      default: {}
    },
    buildId: {
      type: String,
      default: ''
    }
  },
  methods: {
    // swiper change
    swiperChange (e) {
      if (e.detail.source) {
        this.swiperIndex = e.detail.current
      }
    },
    // 点击item
    swiperDetailBtn () {
      uni.navigateTo ({
        url: '/pagesBuild/buildDetail/photoList'
      })
    },
    goToVr (path) {
      new buildModel().setModel(path, 'path')
      uni.navigateTo ({
        url: '/pagesBuild/buildDetail/vrPage'
      })
    },
    // 查看更多
    lookMore () {
      uni.navigateTo ({
        url: '/pagesBuild/buildDetail/photoList?buildId=' + this.buildId
      })
    }
  }
}
